.charity-bar {
  text-align: center;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  padding: 24px 40px;
}

.charity-bar * {
  color: var(--charity-bar-txt-clr);
  font-size: 18px;
}

ol.charity-list {
  counter-reset: li;
  flex-direction: column;
  gap: 20px;
  list-style-type: none;
  display: flex;
}

li.charity-list-item {
  height: 32px;
  align-items: center;
  display: flex;
}

li.charity-list-item:before {
  counter-increment: li;
  content: counter(li, decimal-leading-zero);
  color: var(--charity-bar-txt-clr);
  align-self: center;
  margin-right: 16px;
}

.charity-bar-header {
  color: var(--charity-bar-txt-clr);
  letter-spacing: -.04em;
  margin: 0 0 40px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.2;
  display: flex;
}

.charity-bar-header:after {
  content: url("Ukraine.51f27aed.svg");
  width: 20px;
  height: 20px;
  margin-left: 12px;
}

.charity-fond-img {
  fill: var(--charity-bar-txt-clr);
